import React from 'react';
import PropTypes from 'prop-types';
import styled, { css } from "styled-components";
import routes from '../../routes';
const {Link, Router } = routes;
import renderHTML from 'react-render-html';
import theme from '../../styles/theme';
import Media from '../media';
import IconNew from './IconNew';

const TatCaBaiHocTitle = 'Tất cả các bài';

const ContentWrapper = styled.div`
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
  flex-wrap: wrap;
`;

const BaiHocListWrapper = styled.ul`
  padding: 2px 2px;
  display: flex;
  flex-wrap: wrap;
  overflow-y: scroll;
  width: 100%;
`;

const BaiHocListLI = styled.li`
  background-color: #eee !important;
  display: block;
  text-decoration: none;
  min-height: 23px;
  margin: 1px 1px;
  border-radius: 5px;
  font-size: 10pt;
  font-weight: bold;
  width: 33%;
  padding: 1px !important;
  ${Media.sm`-ms-flex: 0 0 100%;flex: 0 0 100%;max-width: 100%;margin-left: 5px;margin-right: 5px;`};
  border: 1px solid ${theme.colors.textLink};
  ${props =>
    (!props.soChan || props.soChan===0) &&
    css`
        background-color:#fff !important;
      `};
`;

const LinkI = styled(Link)`
  width: 100%;
  min-height: 30px;
  paddingLeft: 5
`;

function getShortContent(content) {
  return `${content.substring(0, 200)}... (xem tiếp)`;
}

const BaiHocItem = ({baihoc, showNew, prefix}) => {
  const item = baihoc;
  
  if (!item || !item.id) {
    return ('');
  }
  
  let linkTo = item.link || `${prefix + item.id}`;
  // if (item.link === TatCaBaiHocTitle) {
  //   linkTo = '/chuong/giaoTrinhDich';
  // }

  const id = item.id;
  const soChan = parseInt(id.substr(1,2).replace('B',''))%2;

  {/* <LinkI key={id} className="linkNdhp" to={linkTo} style={{ paddingLeft: 5 }}> */}
  return (
    <BaiHocListLI soChan={soChan}>
      <LinkI key={id} to={linkTo} >
        {item.title}
        {/* <i className="icon-arrow-right" /> */}
        {/* <IconNew showNew={showNew} /> */}
      </LinkI>
    </BaiHocListLI>
  );
};

BaiHocItem.propTypes = {
  baihoc: PropTypes.shape().isRequired,
  showNew: PropTypes.bool
};

const BaiHocListHome = ({ loading, baiHocs, linkAll, prefix }) => {
  const baiHocAll = baiHocs;
  let i = 0;
  const itemAll = {
    chuong: 'B - Chương 1',
    content: 's',
    id: 'C0B0',
    title: TatCaBaiHocTitle,
    link: linkAll,
  };
  let cards = '';
  if(baiHocAll){
    baiHocAll.push(itemAll);
    cards = baiHocAll.map(item => (
      <BaiHocItem key={`${linkAll + ++i}`} baihoc={item} showNew={i===1} prefix={prefix}/>
    ));
  }
  return (
    <ContentWrapper>
      <BaiHocListWrapper>
        {cards}
      </BaiHocListWrapper>
    </ContentWrapper>
  );
};

BaiHocListHome.propTypes = {
  baiHocs: PropTypes.arrayOf(PropTypes.shape()).isRequired,
  loading: PropTypes.bool.isRequired,
};

BaiHocListHome.defaultProps = {
};

export default BaiHocListHome;
